import React, { Component } from "react";

class TextForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      message: "",
      upperchecked: true,
      lowerchecked: false,
      puncchecked: false,
      extraspa: false,
    };
  }
  punctuations=[',','.','-','_','!','?',':',';','(',')','[',']','/','"',"\'"]
  Changetext = (event) => {
    this.setState({
      message: event.target.value,
    });
  };
  uppercasehandler = (event) => {
    // console.log(this.state.upperchecked);
    this.setState({
      upperchecked: !this.state.upperchecked,
    });
  };
  lowerhandler = (event) => {
    this.setState({
      lowerchecked: !this.state.lowerchecked,
    });
  };
  removepunc = (event) => {
    this.setState({
      puncchecked:!this.state.puncchecked,
    });
  };
  extraspacehandle = (event) => {
    this.setState({
      extraspa: !this.state.extraspa,
    });
  };
  returnstring(msg) {
    if(this.state.upperchecked) 
    {
      return msg.toUpperCase();
    }
    else if(this.state.lowerchecked) 
    {
      return msg.toLowerCase();
    }
    else if(this.state.puncchecked)
    {
        let newstr="";
        for (let index = 0; index < this.state.message.length; index++) {
          if(this.punctuations.includes(this.state.message[index]))
          {
          }
          else newstr+=this.state.message[index]
        }
        return newstr
    }
    else if(this.state.extraspa)
    {
        let newstr="";
        for (let index = 0; index < this.state.message.length; index++) {
          if(this.state.message[index]==" " && this.state.message[index+1]==" ")
          {
          }
          else newstr+=this.state.message[index]
        }
        return newstr
    }
    return this.state.message;
  }
  Submithandler() {
    this.setState({
      message: this.returnstring(this.state.message),
    });
  }
  render() {
    let wordsmessage = this.state.message ? (
      <h6>
        No of words in the text: {this.state.message.split(" ").length - 1}
      </h6>
    ) : (
      ""
    );
    let charmessage = this.state.message ? (
      <h6>No of charecters in the text: {this.state.message.length}</h6>
    ) : (
      ""
    );
    return (
      <div className="container">
        <div className="mb-3">
          <label htmlFor="exampleFormControlTextarea1" className="form-label">
            <h1>{this.props.heading}</h1>
          </label>
          <br />
          <textarea
            className="form-control"
            id="exampleFormControlTextarea1"
            rows="8"
            value={this.state.message}
            onChange={this.Changetext}
          ></textarea>
          <br />
          {wordsmessage}
          {charmessage}
        </div>
        <div className="form-check form-switch">
          <input
            className="form-check-input Uppercase"
            type="checkbox"
            defaultChecked={true}
            id="flexSwitchCheckChecked"
            onClick={this.uppercasehandler}
          />
          <label className="form-check-label" htmlFor="flexSwitchCheckChecked">
            Convert to Uppercase
          </label>
        </div>
        <div className="form-check form-switch">
          <input
            className="form-check-input Uppercase"
            type="checkbox"
            name="checkbox"
            id="flexSwitchCheckChecked"
            onClick={this.removepunc}
          />
          <label className="form-check-label" htmlFor="flexSwitchCheckChecked">
            Remove Punctuation
          </label>
        </div>
        <div className="form-check form-switch">
          <input
            className="form-check-input lowercase"
            type="checkbox"
            name="checkbox"
            id="flexSwitchCheckDefault"
            onClick={this.lowerhandler}
          />
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">
            Convert to lowercase
          </label>
        </div>
        <div className="form-check form-switch">
          <input
            className="form-check-input Removeextras"
            type="checkbox"
            name="checkbox"
            id="flexSwitchCheckDefault"
            onClick={this.extraspacehandle}
          />
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">
            Remove Extra spaces
          </label>
        </div>
        <button
          className="btn btn-primary my-3"
          onClick={this.Submithandler.bind(this)}
        >
          Analyze Text
        </button>
      </div>
    );
  }
}

export default TextForm;
